<template>
  <div class="recommend">
    <pull-to class="recommend-content"
             :top-load-method="refresh"
             :bottom-load-method="loadmore"
             @bottom-state-change="stateChange">
      <div>
        <div v-if="recommends.length" class="slider-wrap">
          <swiper :options="swiperOption">
            <swiper-slide v-for="item in recommends" :key="item.linkUrl">
              <a :href="item.linkUrl">
                <img :src="item.picUrl" class="needsclick">
              </a>
            </swiper-slide>
            <div class="swiper-pagination" slot="pagination"></div>
          </swiper>
        </div>
        <div class="recommend-list">
          <h1 class="list-title">热门歌单推荐</h1>
          <ul>
            <li v-for="item in discList" class="item">
              <div class="icon">
                <img width="60" height="60" v-lazy="item.imgurl">
              </div>
              <div class="text">
                <h2 class="name" v-html="item.creator.name"></h2>
                <p class="desc" v-html="item.dissname"></p>
              </div>
            </li>
          </ul>
        </div>
      </div>
      <div class="loading-container" v-show="!discList.length">
        <loading></loading>
      </div>
    </pull-to>
  </div>
</template>

<script type="text/ecmascript-6">
  import Loading from 'base/loading/loading'
  import PullTo from 'vue-pull-to'
  import { swiper, swiperSlide } from 'vue-awesome-swiper'
  import { getRecommend, getDiscList } from 'api/recommend'
  import { ERR_OK } from 'api/config'
  require('swiper/dist/css/swiper.css')

  export default {
    data() {
      return {
        recommends: [],
        discList: [],
        swiperOption: {
          autoplay: 1500,
          direction: 'horizontal',
          pagination: '.swiper-pagination',
          loop: true,
          autoplayDisableOnInteraction: false,
          observer: true,
          observeParents: true
        }
      }
    },
    computed: {},
    created() {
      this._getRecommend()
      this._getDiscList()
    },
    mounted() {
    },
    methods: {
      _getRecommend() {
        getRecommend().then((res) => {
          if (res.code === ERR_OK) {
            this.recommends = res.data.slider
          }
        })
      },
      _getDiscList() {
        getDiscList().then((res) => {
          if (res.code === ERR_OK) {
            this.discList = res.data.list
          }
        })
      },
      refresh(loaded) {
        setTimeout(() => {
          this.discList.reverse()
          loaded('done')
        }, 2000)
      },
      loadmore(loaded) {
        setTimeout(() => {
          this.dataList = this.discList.concat(this.discList)
          loaded('done')
        }, 2000)
      },
      stateChange(state) {
        if (state === 'pull' || state === 'trigger') {
          this.iconLink = '#icon-arrow-bottom'
        } else if (state === 'loading') {
          this.iconLink = '#icon-loading'
        } else if (state === 'loaded-done') {
          this.iconLink = '#icon-finish'
        }
      }
    },
    components: {
      swiper,
      swiperSlide,
      PullTo,
      Loading
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  .recommend
    position: fixed
    width: 100%
    top: 88px
    bottom: 0
    overflow: hidden
    .recommend-content
      .slider-wrap
        min-height: 1px
        .swiper-container
          position: relative
          overflow: hidden
          white-space: nowrap
          .swiper-wrapper
            float: left
            box-sizing: border-box
            overflow: hidden
            text-align: center
            a
              display: block
              width: 100%
              overflow: hidden
              text-decoration: none
            img
              display: block
              width: 100%
      .recommend-list
        .list-title
          height: 65px
          line-height: 65px
          text-align: center
          font-size: $font-size-medium
          color: $color-theme
        .item
          display: flex
          box-sizing: border-box
          align-items: center
          padding: 0 20px 20px 20px
          .icon
            flex: 0 0 60px
            width: 60px
          .text
            display: flex
            flex-direction: column
            justify-content: center
            flex: 1
            line-height: 20px
            overflow: hidden
            padding-left: 20px
            font-size: $font-size-medium
            .name
              margin-bottom: 10px
              color: $color-text
            .desc
              color: $color-text-d
      .loading-container
        position: absolute
        width: 100%
        top: 50%
        transform: translateY(-50%)
</style>



